import React from 'react';
import Align from 'rc-align';
import ReactDOM from 'react-dom';


class Demo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      align: {
        points: ['cc', 'cc']
      }
    };
  }

  getTarget() {
    console.log(this);
    return this.refs.container;
  }

  componentDidMount() {
    console.log('2222', this.refs);
  }


  render() {
    return (
      <div ref="container" id="container" style={{
        width: 500,
        height: 500,
        border: '1px solid red'
      }}>
        <Align
          target={() => { console.log(this.refs); return document.getElementById('container'); }}
          align={this.state.align}
        >
          <div style={{
            position: 'absolute',
            width: 100,
            height: 100,
            background: 'yellow',
          }}>
            target is parent
          </div>
        </Align>
      </div>
    );
  }
}


ReactDOM.render(<Demo />, document.getElementById('demo2'));
